* {
  margin: 0px;
  padding: 0px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.app{
  position: relative;
  display: flex;
  flex-flow: column;
  width: 100%;
}

body{
  background-color: whitesmoke;
  /* overflow: hidden; */
}

/* Margins Starts Here */
/* ++++++++++++LEVEL 1+++++++++++++ */
.margin-left-1{
  margin-left: 1vw;
}
.margin-right-1{
  margin-right: 1vw;
}
.margin-top-1{
  margin-top: 1vw;
}
.margin-bottom-1{
  margin-bottom: 1vw;
}
/* ++++++++++++LEVEL 2+++++++++++++ */
.margin-left-2{
  margin-left: 2vw;
}
.margin-right-2{
  margin-right: 2vw;
}
.margin-top-2{
  margin-top: 2vw;
}
.margin-bottom-2{
  margin-bottom: 2vw;
}

/* Margins Ends Here */


.padding-1{
  padding: 5px;
}
.padding-left-1{
  padding-left: 5px;
}
.padding-right-1{
  padding-right: 5px;
}
.padding-top-2{
  padding-top: 1px;
}
.padding-bottom-2{
  padding-bottom: 1px;
}

.container{
  display: flex;
  flex-flow: row;
  position: relative;
  width: 100%;
}


.positioning{
  display: grid;
  padding-left: 250px;
  width: 100%;
  position: relative;
}
